html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none; }

html {
  height: 101%; }

/* always display scrollbars */
body {
  font-size: 62.5%;
  line-height: 1;
  font-family: Arial, Tahoma, Verdana, sans-serif; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none; }

strong {
  font-weight: bold; }

input {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  border: 0;
  max-width: 100%; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

body {
  color: #484848;
  font-family: "FiraSans", "Arial", "sans-serif";
  font-weight: 400;
  font-size: 15px; }

.container {
  max-width: 1366px;
  margin: 32px auto; }
  @media screen and (max-width: 768px) {
    .container {
      max-width: 768px;
      margin: 20px auto; } }
  @media screen and (max-width: 550px) {
    .container {
      max-width: 550px;
      margin: 10px auto; } }
  @media screen and (max-width: 430px) {
    .container {
      max-width: 430px;
      margin: 0; } }

.main {
  height: 906px; }
  .main__bg {
    position: relative;
    background-image: url(../img/bg.jpg);
    background-repeat: no-repeat;
    background-position-y: -30px;
    background-position-x: center;
    display: block;
    margin-top: -50px; }
    @media screen and (max-width: 1024px) {
      .main__bg {
        background-size: 1024px auto;
        background-position-y: 0; } }
    @media screen and (max-width: 768px) {
      .main__bg {
        background-size: 738px auto; } }
    @media screen and (max-width: 550px) {
      .main__bg {
        background-image: url(../img/bg_mobile.jpg);
        background-position-y: 45px;
        background-size: 870px; } }
  .main__body {
    position: relative;
    height: 906px; }
  .main__content {
    max-width: 343px;
    text-align: center;
    display: flex;
    flex-flow: column;
    align-items: center;
    padding: 510px 0 20px 0;
    margin: 0 auto; }
    @media screen and (max-width: 1024px) {
      .main__content {
        padding-top: 360px; } }
    @media screen and (max-width: 768px) {
      .main__content {
        padding-top: 240px; } }
    @media screen and (max-width: 550px) {
      .main__content {
        padding-top: 320px; } }
    @media screen and (max-width: 430px) {
      .main__content {
        padding-top: 345px;
        max-width: 288px; } }
  .main__sub-title {
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
    color: #ffffff;
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    margin-bottom: 35px; }
    @media screen and (max-width: 768px) {
      .main__sub-title {
        margin-bottom: 20px; } }
    @media screen and (max-width: 550px) {
      .main__sub-title {
        margin-bottom: 40px; } }
    @media screen and (max-width: 430px) {
      .main__sub-title {
        font-size: 20px; } }
  .main__scroll-body {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 76px; }
  .main__scroll-tip {
    display: inline-block;
    border: dashed white 1px;
    padding: 11px;
    border-radius: 50px;
    cursor: pointer; }
  .main__scroll-tail {
    width: 2px;
    height: 56px;
    background-color: white; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 114px;
  z-index: 1;
  position: relative;
  height: 52px; }
  @media screen and (max-width: 768px) {
    .menu {
      padding: 0 13px;
      flex-flow: wrap; } }
  @media screen and (max-width: 320px) {
    .menu {
      padding: 0 3px; } }
  .menu__number {
    font-weight: 500;
    text-decoration: none;
    color: #484848;
    white-space: nowrap; }
    @media screen and (max-width: 768px) {
      .menu__number {
        order: 2; } }
    @media screen and (max-width: 550px) {
      .menu__number {
        margin-left: 55px; } }
    @media screen and (max-width: 430px) {
      .menu__number {
        margin-left: auto;
        font-size: 14px; } }
  @media screen and (max-width: 550px) {
    .menu__logo-link {
      margin: 20px 50px 0 0; } }
  @media screen and (max-width: 430px) {
    .menu__logo-link {
      margin: 20px 14px 0 0; } }
  @media screen and (max-width: 360px) {
    .menu__logo-link {
      margin: 25px 5px 0 0; } }
  .menu__logo {
    width: 137px;
    height: 48px; }
    @media screen and (max-width: 320px) {
      .menu__logo {
        width: 120px;
        height: 42px; } }
  .menu__ring-me {
    border: none;
    outline: none;
    background: none;
    font-size: 15px;
    font-weight: 500;
    color: #484848;
    font-family: "FiraSans", "Arial", "sans-serif";
    padding: 0;
    border-bottom: 1px dashed #484848;
    text-transform: uppercase;
    cursor: pointer;
    white-space: nowrap; }
    @media screen and (max-width: 768px) {
      .menu__ring-me {
        order: 3; } }
    @media screen and (max-width: 550px) {
      .menu__ring-me {
        margin-left: auto;
        margin-top: -16px; } }
    @media screen and (max-width: 430px) {
      .menu__ring-me {
        font-size: 14px;
        text-transform: none; } }

